	data() {
		return {

		}
	},
	methods:{
		
	
	jump(url){
		if(url){
			if(url == ''){
				uni.navigateBack({
					delta:1
				})
			}else if(url == "/pages/main/tradingCenter" || url == "/pages/main/my" || url == "/pages/main/index" || url == "/pages/main/financial" || url == "carService"){//判断是不是tabbar页面
				uni.switchTab({
					url:url
				})
			}else{
				//跳转到有返回跳转的页面
				uni.navigateTo({
					url:url
				})
			}
		}else{
			uni.navigateBack({
				delta:1
			})
		}
	},
	jum(url){
		if(url){
			if(url == ''){
				uni.navigateBack({
					delta:1
				})
			}else if(url == "/pages/main/tradingCenter" || url == "/pages/main/my" || url == "/pages/main/index" || url == "/pages/main/financial" || url == "carService"){//判断是不是tabbar页面
				uni.switchTab({
					url:url
				})
			}else{
				//跳转到有返回跳转的页面
				uni.navigateTo({
					url:url
				})
			}
		}else{
			uni.navigateBack({
				delta:1
			})
		}
	}
	}
	
}
